@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&family=Staatliches&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */

.App-header {
  background-color: #282c34;
}

h1 {
  text-align: center !important;
  margin-top: 50px !important;
  margin-bottom: 0px !important;
  font-size: 40px !important;
  font-weight: 500 !important;
  font-family: cursive;
}

.about {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 30px 15px;
}
.about .container-lg {
  display: flex;
  justify-content: center;
  padding: 0 !important;
  height: 500px;
  box-shadow: 5px 4px 12px gray;
  margin: 20px 0;
}
.about .left-side {
  width: 50%;
}
.about .left-side img {
  height: 500px !important;
}
.about .container-lg > .right-side {
  display: flex;
  justify-content: flex-start !important;
  align-items: flex-start;
  flex-direction: column;
  padding: 30px 40px;
}
.about .container-lg .right-side .about-detail {
  font-size: 15px !important;
  font-family: sans-serif;
  color: rgb(63, 58, 58);
  line-height: normal;
}
.about .container-lg .right-side h5 {
  font-size: 20px !important;
  padding-top: 30px;
  padding-bottom: 10px;
  color: rgb(63, 58, 58);
}
.container-lg .right-side .fa-circle {
  color: #ff5823;
  margin-right: 10px;
}
.about .container-lg .right-side p {
  color: rgb(63, 58, 58);
  font-size: 15px !important;
  line-height: 0.2px;
}

.about .container-lg .hire-btn {
  background-color: #24263b !important;
}
.container-lg .res-btn:hover {
  background-color: #ff5823 !important;
  color: #24263b !important;
}
@media (max-width: 800px) {
  .about .left-side {
    display: none;
  }
  .about {
    padding: 0 30px;
  }
}
@media (max-width: 450px) {
  .about {
    padding: 0;
  }
  .about .container-lg > .right-side {
    padding: 30px 10px;
  }
}

.contact {
  background-color: #24263b;
  border-radius: 20px;
  color: white;
  padding: 20px !important;
  height: auto;
  margin-top: 20px;
}
a{
  text-decoration: none;
  color: #888996;
}
a:hover{
  color: #ff5823;
}

.contact .social-icons h2 {
  margin-right: 10px;
  letter-spacing: 2px;
  font-size: 30px;
  font-weight: 800;
}
.contact .social-icons .icons {
  letter-spacing: 2px;
  width: 250px;
  display: flex;
  justify-content: space-around;
}
.contact .social-icons .icons .fab {
  font-size: 40px;
  cursor: pointer;
  margin-bottom: 0;
}

.contact .form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80%;
}
.contact .form-container .img-container {
  width: 50%;
  margin-right: 15px;
}
.contact .form-container .img-container h3 {
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #888996;
  margin-left: 5px;
}
.contact .form-container .userForm {
  width: 50%;
  padding: 15px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  height: 510px;
  border-radius: 15px;
}
.contact .form-container .userForm label {
  color: #24263b;
  margin: 10px 0 5px 8px;
  font-size: 16px;
  font-weight: 600;
}

.contact .form-container .userForm input {
  width: 100%;
  height: 45px;
  padding: 5px 10px;
  margin: 5px 0;
  background-color: #f5efef;
  border-radius: 15px;
  border: none;
  outline: none;
}
.contact .form-container .userForm input:focus {
  outline: none;
  border: 2px solid #a5aae4 !important;
  background-color: white;
}
.contact .form-container .userForm textarea {
  height: 100px !important;
  padding: 5px 10px;
  border-radius: 10px;
  border: none;
  background-color: #f5efef;
}
.contact .form-container .userForm textarea:focus {
  border: 2px solid #a5aae4;
  background-color: white;
  outline: none;
}
.contact .form-container .userForm .btn-default {
  background-color: #24263b !important;
  font-size: 18px;
  padding: 6px 15px;
  width: 130px;
  margin-top: 20px;
}

@media (max-width: 800px) {
  .contact {
    height: auto;
    border-radius: 0;
  }
  .contact .form-container {
    flex-direction: column;
  }
  .contact .form-container .img-container {
    width: 60%;
    margin-right: 0;
  }
  .contact .form-container .userForm {
    width: 60%;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
}

@media (max-width: 700px) {
  .contact .form-container .img-container {
    width: 70%;
  }
  .contact .form-container .userForm {
    width: 70%;
  }
}
@media (max-width: 600px) {
  .contact .form-container .img-container {
    width: 80%;
  }
  .contact .form-container .userForm {
    width: 80%;
  }
}
@media (max-width: 500px) {
  .contact .form-container .img-container {
    width: 100%;
  }
  .contact .form-container .userForm {
    width: 100%;
  }
}

.nav-backgroud {
  background-color: #24263b;
  color: white;
  height: 75px;
  display: flex;
  /* position: sticky; */
  top: 0;
  z-index: 9;
}
.nav-backgroud > .menu {
  display: flex;
  align-items: center !important;
  justify-content: space-around;
}
.nav-logo {
  font-size: 30px;
  font-weight: 700;
}

.navbar-nav {
  flex-direction: row !important;
  align-items: center;
}
.nav-link {
  padding-right: 15px !important;
  padding-left: 15px !important;
  color: white !important;
  font-weight: 600;
  font-size: 18px;
}
.nav-link:hover {
  color: #ff5823 !important;
}

.menu-bar {
  display: none;
}

@media (max-width: 770px) {
  .menu-bar {
    display: flex;
    font-size: 30px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 12px;
    z-index: 10000;
  }
  .nav-backgroud > .menu {
    justify-content: space-between;
    flex-direction: row-reverse;
  }
  .navbar-nav {
    flex-direction: column !important;
    justify-content: center;
    position: fixed;
    top: 0px;
    width: 100%;
    left: -100%;
    bottom: 0;
    z-index: 999;
    background: #2c2e41;
    transition: 0.2s ease-in;
  }
  .active {
    left: 0;
  }
  .navbar-nav > .nav-item {
    padding: 15px;
  }
}
@media (max-width: 450px) {
  .nav-logo {
    font-size: 25px !important;
  }
}

.home {
  height: 90vh;
  background: #24263b;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-between;
  -webkit-clip-path: polygon(
    0 0,
    100% 0,
    100% 95%,
    90% 95%,
    85% 100%,
    80% 95%,
    75% 100%,
    70% 95%,
    45% 95%,
    50% 100%,
    55% 95%,
    30% 95%,
    25% 100%,
    20% 95%,
    15% 100%,
    10% 95%,
    0% 95%
  );
          clip-path: polygon(
    0 0,
    100% 0,
    100% 95%,
    90% 95%,
    85% 100%,
    80% 95%,
    75% 100%,
    70% 95%,
    45% 95%,
    50% 100%,
    55% 95%,
    30% 95%,
    25% 100%,
    20% 95%,
    15% 100%,
    10% 95%,
    0% 95%
  );
  padding: 0 20px !important;
}

.left-side {
  width: 65%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.left-side > h4 {
  font-size: 23px;
  font-weight: 400;
}
.left-side > h4 > span {
  font-family: "Staatliches", cursive;
  font-size: 30px;
  font-weight: 500;
  color: #ff5823;
  text-transform: uppercase;
}
.left-side > h2 {
  font-family: cursive;
  text-transform: capitalize;
  font-size: 30px;
  margin: 35px 0;
}
.left-side > p {
  font-family: sans-serif;
  margin-bottom: 30px;
  font-size: 18px;
  text-align: center;
}

.bnt-container {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
.btn-default {
  padding: 10px 12px;
  margin-right: 5px;
  border-radius: 50px;
  width: 120px;
  font-weight: 500;
  border: none;
  font-size: 14px;
  transition: 180ms ease-in;
  color: white;
}
.btn-default:nth-child(1) {
  border: 2px solid #ff5823;
  background: transparent;
}
.btn-default:nth-child(1):hover {
  border: 2px solid white;
}
.btn-default:nth-child(2) {
  text-decoration: none;
  text-align: center;
  border: 2px solid #ff5823;
  background: #ff5823;
}
.btn-default:nth-child(2):hover {
  background: white;
  border: 2px solid white;
  color: #24263b;
}

.home .right-side {
  width: 35vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home .right-side .clip-circle {
  background-color: white;
  padding: 0 1px 10px 1px;
  -webkit-clip-path: circle(49%);
          clip-path: circle(49%);
}
.home .right-side .clip-circle .w-100 {
  width: 300px !important;
  border-radius: 50%;
}

a{
  text-align: center;
}


@media (max-width: 800px) {
  .home {
    flex-direction: column-reverse;
    height: auto;
    padding: 70px 0 !important;
  }
  .home .right-side {
    margin-bottom: 50px;
  }
}
@media (max-width: 450px) {
  .home .left-side > h4 {
    font-size: 18px;
  }
  .home .left-side > h4 > span {
    font-size: 20px;
  }
  .home .left-side > h2 {
    font-size: 25px;
  }

  .btn-default {
    padding: 6px 8px;
    margin-right: 0;
    width: 100px;
    font-size: 12px;
  }
}

.resume {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.resume p {
  margin-bottom: 0 !important;
}

.resume .left-side {
  flex-direction: row;
  width: 34%;
  border: none;
  justify-content: flex-start;
  padding: 0 30px 0 0 !important;
  margin-right: 20px;
  box-shadow: 15px 0 9px -15px #1f2235;
  height: 450px;
}
.accordion-items {
  border: none;
  padding: 0 !important;
}
.resume .left-side .bullet-icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column !important;
  background-color: #24263b;
  width: 40px;
  height: 100%;
}
.resume .left-side .bullet-icons .fas {
  color: white !important;
  font-size: 20px;
  margin: 0 !important;
}
.resume .left-side .bullet-text {
  display: flex;
  flex-direction: column !important;
  justify-content: space-evenly !important;
  padding: 10px 0 !important;
  height: 100% !important;
  width: 100% !important;
}
.resume .left-side .bullet-text .accordion-button {
  font-size: 17px !important;
  font-weight: 700;
  padding: 6px 0 !important;
  padding-left: 20px !important;
  transition: 0.3s ease-in !important;
  border-radius: 0 20px 20px 0 !important;
  text-transform: capitalize;
}
.accordion-button:not(.collapsed) {
  color: white;
  background-color: #24263b !important;
  box-shadow: none;
}

.resume .right-side {
  width: 66%;
  height: 500px;
  overflow: hidden;
  border: none;
}
.accordion-button::after,
.accordion-button::before {
  display: none;
}
.resume .educations,
.resume .work,
.resume .project,
.resume .interest,
.resume .skills {
  width: 100%;
  height: 100%;
}

.resume .accordion-body {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  padding: 10px !important;
}
.resume .accordion-body .edu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.resume .accordion-body .edu .uni {
  font-size: 16px !important;
  font-weight: 700;
  margin-bottom: 5px;
  color: #ff5823;
}
.resume .accordion-body .edu .deg {
  font-size: 14px !important;
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 25px;
}
.resume .accordion-body .edu p {
  font-size: 13px !important;
}
.resume .accordion-body .edu p,
.resume .accordion-body .works p {
  font-size: 14px !important;
}
.resume .accordion-body .institute {
  width: 80%;
}
.resume .accordion-body .des {
  margin-left: 25px !important;
  margin-top: 15px;
}
.resume .accordion-body .edu .year {
  width: 100px !important;
  font-size: 13px !important;
  font-weight: 600;
  text-align: center;
  background-color: #ff5823;
  padding: 6px 10px;
  border-radius: 50px;
  color: white;
  text-decoration: none;
}

.resume .accordion-body .skill {
  display: flex;
  margin-bottom: 10px !important;
  align-items: center;
}
.resume .accordion-body .skill .uni {
  color: #ff5823;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 5px;
}
.progress {
  height: 20px;
  margin-left: 25px;
}
.progress-bar {
  background-color: #1f2235 !important;
  font-weight: 700;
}

.workExp{
  margin-top: 20px;
}


@media (max-width: 800px) {
  .resume {
    flex-direction: column;
    padding: 30px;
  }
  .resume .left-side {
    width: 100% !important;
    margin-bottom: 20px;
  }
  .resume .right-side {
    width: 100% !important;
  }
  .resume .left-side .bullet-text {
    width: 50% !important;
  }
  .resume .accordion-body {
    padding: 0 !important;
  }
}

@media (max-width: 450px) {
  .resume {
    padding: 10px;
  }
  .resume .left-side {
    padding: 0 !important;
  }
  .resume .left-side .bullet-text .accordion-button {
    font-size: 14px !important;
    padding-left: 10px !important;
  }
  .resume .left-side .bullet-text {
    width: 80% !important;
  }
}

